import React from 'react'
import SVG from 'react-inlinesvg'
import Loading from 'public/loading.svg'


import './button.scss'

export default function Button({
  label = '',
  className = 'primary',
  style = {},
  disabled = false,
  onClick = null,
  svg_left = null,
  svg_right = null,
  loading = false,
  onRemove = null,
  remove = false,
  file = null,
  download = null,
  onDownload = null,
}) {
  return (
    <button
      className={`btn ${className ? `btn-${className}` : ''}`}
      style={style}
      disabled={disabled}
      onClick={onClick}
    >
      {loading && (
        <div className="btn-loading">
          <SVG src={Loading} />
        </div>
      )}
      {svg_left && (
        <div className="svg-left">
          <SVG src={svg_left} />
        </div>
      )}
      {label && <span>{label}</span>}
      
     
      {svg_right && (
        <div className={`svg-right`}>
          <SVG src={svg_right} />
        </div>
      )}
    </button>
  )
}
