const HomePage = (props) => {
  const history = useHistory();


  const [iin, setIIN] = React.useState("");
  const [isOpen, setIsOpen] = React.useState(false);
  const [correctIin, setCorrectIin] = React.useState(false);


  const [tableData, setTableData] = useState([
    {
      id: "№ 135 от 12.12.2020 Ипотека",
      borrower_full_name: "100000",
      borrower_iin: "10000",
      fines_payment: "1000",
      input_item: "11000",
      сhoosed_item: false,
    },
    {
      id: "№ 135 от 12.12.2020 Товарный кредит",
      borrower_full_name: "120000",
      borrower_iin: "50000",
      fines_payment: "3000",
      input_item: "53000",
      сhoosed_item: false,
    },
    {
      id: "№ 135 от 12.12.2020 Ипотека",
      borrower_full_name: "140000",
      borrower_iin: "10000",
      fines_payment: "2000",
      input_item: "12000",
      сhoosed_item: false,
    },
  ]);

  const handleTableChange = (index, field, value) => {
    let newTableData = [];
    if (tableData.length > 0) {
      for (let i = 0; i < tableData.length; i++) {
        if (index !== i) {
          newTableData.push(tableData[i]);
        } else {
          newTableData.push({ ...tableData[i], [field]: value });
        }
      }

      setTableData(newTableData);
    }
  };

  const callBack=(res)=>{
    setCorrectIin(res);
  }
  const mobile = window.innerWidth < 600;
  return (
    <Layout>
    <div className="home">


<Modal isOpen={isOpen} handleFunc={()=>setIsOpen(false)}>
         <OtpModal/>
        </Modal>
        {mobile && <section style={{paddingLeft:"20px", paddingRight:"20px",marginTop:"40px"}}>
         
          <InputIin
            border
            value={iin}
            mask="99 99 99 99 99 99"
            callBack={callBack}
            onChange={(e) => setIIN(e.target.value)}
            className={validateIIN(iin) ? "error" : ""}
            placeholder={"Введите ИИН"}
          />
   <div style={{display:"flex", justifyContent:"center"}}>
          <Button
            style={{ width: "100%", marginBottom: "20px" }}
            className={"success"}
            onClick={()=>setIsOpen(true)}
            label="Далее"
            disabled={!correctIin}
          />
        </div>
         
        </section>}


      <div className="container">
        <section className="home__instruction">
          <section style={{ width: "286px" }}>
            {" "}
            <SVG src={Instruction1} />
          </section>
          <section style={{ width: "286px" }}>
            {" "}
            <SVG src={Instruction2} />
          </section>
          <section style={{ width: "286px" }}>
            {" "}
            <SVG src={Instruction3} />
          </section>
          <section style={{ width: "286px" }}>
            {" "}
            <SVG src={Instruction4} />
          </section>
        </section>
        {!mobile && <section className="card">
          <div className="card_box">
          <div className="card_box-small">
          <InputIin
            border
            value={iin}
            mask="99 99 99 99 99 99"
            callBack={callBack}
            onChange={(e) => setIIN(e.target.value)}
            className={validateIIN(iin) ? "error" : ""}
            placeholder={"Введите ИИН"}
          />
   <div style={{display:"flex", justifyContent:"center"}}>
          <Button
            style={{ width: "100%", marginBottom: "20px" }}
            className={"success"}
            onClick={()=>setIsOpen(true)}
            label="Далее"
            disabled={!correctIin}
          />
        </div>
          </div>
          </div>
        </section>}
      </div>


     
    </div>
    </Layout>
  );
};

export default HomePage;

