import React from 'react'
import classNames from 'classnames'
import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent';
import Input from '@/components/input/index'
import { TooltipSvg } from '../TooltipSvg';
import './styles.scss'

export const ContractCard = ({ isSelected, className, onToggleSelect }) => {
  return (
    <Card className={classNames("contract-card", className, { "contract-card--selected": isSelected })}>
      <CardContent>
        <div className="contract-card__checkbox checkbox bounce">
          <input
            type="checkbox"
            checked={isSelected}
            onChange={onToggleSelect}
          />
          <svg viewBox="0 0 21 21">
            <polyline points="5 10.75 8.5 14.25 16 6"></polyline>
          </svg>
        </div>
        <ContractCardItem 
          label='Информация о договоре' 
          value='№ 135 от 12.12.2020 Ипотека' 
          style={{width: 'calc(100% - 30px)'}}
        />
        <div className="contract-card__row">
          <ContractCardItem label='Остаток задолженности' value='10 000 ₸' />
          <ContractCardItem label='Сумма для частично досрочного погашения' value='30 000 ₸' />
        </div>
        <div className="contract-card__row">
          <ContractCardItem 
            label={
              <div className="contract-card__tooltip-text">
                <span>Штрафы,<br/> пени</span>
                <TooltipSvg message='Сумма штрафов и пени всегда включена в сумму общего платежа' />
              </div>
            } 
            value={
              <span className="contract-card__red">
                1 000 ₸
              </span>
            } 
          />
          <ContractCardItem label={<>Переплата<br/> по займу</>} value='0 ₸' />
        </div>
        
        <ContractCardItem
          label={
            <>
            К оплате
            <TooltipSvg message="Поле доступно для редактирования. Не забудьте внести сумму с учетом штрафов или пени, в случае их наличия" />
          </>
          }
          value={ <Input
            value={''}
            onChange={(e) => {}
            }
            border
            disabled={false}
          />}
        />
      </CardContent>
    </Card>
  )
}

const ContractCardItem = ({ label, value, style = {} }) => {
  return (
    <div className="contract-card-item" style={style}>
      <div className="contract-card-item__label">
        { label }
      </div>
      <div className="contract-card-item__value">
        { value }
      </div>
    </div>
  )
}