import React, { useState } from "react";
import SVG from "react-inlinesvg";
import QuestionMark from "public/questionMark.svg";
import { Tooltip, Input } from "@/components";
import "./table.scss";

function Table(props) {
  const [selectedRows, setSelectedRows] = useState([]);

  const actionCheckbox = (index) => {
    let findedIndex = selectedRows.indexOf(index);
    if (findedIndex >= 0) {
      let copyIndexes = [...selectedRows];
      copyIndexes.splice(findedIndex, 1);
      setSelectedRows(copyIndexes);
    } else {
      setSelectedRows([...selectedRows, index]);
    }
  };

  const tableHeaders = (
    <div className="d-table-th">
      {props.tableTitle.map((column, i) => {
        return (
          <div className="d-table-td" key={i}>
            <div className="d-table-td-center">
              {column.name}
              {column.tooltipText && (
                <Tooltip message={column.tooltipText}>
                  <span className="d-table-td-tooltip">
                    <SVG src={QuestionMark} />
                  </span>
                </Tooltip>
              )}
            </div>
          </div>
        );
      })}
    </div>
  );

  const renderByType = (type, value, idx, field, checkedBox) => {
   
    switch (type) {
      case "input":
        return (
          <Input
            value={value}
            onChange={(e) =>
              props.handleTableChange(idx, field, e.target.value)
            }
            border
            disabled={false}
            style={{ maxWidth: "125px" }}
          />
        );
      case "checkbox":
        return (
          <div className="checkbox bounce">
            <input
              type="checkbox"
              defaultChecked={!!checkedBox}
              onChange={() => props.handleTableChange(idx, field, !checkedBox)}
            />
            <svg viewBox="0 0 21 21">
              <polyline points="5 10.75 8.5 14.25 16 6"></polyline>
            </svg>
          </div>
        );

      case "currency":
        return <span>{new Intl.NumberFormat("ru-RU").format(value)} ₸</span>;
      case "fines":
        return (
          <span style={{ color: "#FF0000" }}>
            {new Intl.NumberFormat("ru-RU").format(value)} ₸
          </span>
        );
      default:
        return (
          <div style={{ maxWidth: "170px", fontSize: "15px" }}>{value}</div>
        );
    }
  };

  const tableBody = props.tableData.map((row, idx) => {
    let style = { background: "#FBFBFB" };
    if (row?.сhoosed_item) {
      style = { background: "#EEFBEF", color: "#000", borderBottom:"1px solid #2DA04D", paddingBottom:"13px" };
    }
    return (
      <div className="d-table-tr" key={idx} >
        {props.tableTitle.map((column, i) => {
          return (<div className="d-table-td" key={i} style={style}>
            {renderByType(
              column?.type,
              row[column?.field],
              idx,
              column?.field,
              row?.сhoosed_item
            )}
          </div>)
})}
      </div>
    );
  });


  return (
    <div className="d-table">
      {tableHeaders}
      {tableBody}
    </div>
  );
}

export default Table;
