import React, { useEffect } from 'react'
import InputMask from 'react-input-mask'
import './input.scss'

export default function Input({
  label = '',
  style = {},
  disabled = false,
  mask = '',
  value = '',
  onChange = null,
  placeholder = null,
  name = null,
  border = false,
  shadow = false,
  className = null,
  autoFocus = false,
  type = 'text',
}) {
  const input_ref = React.useRef(null)

  return (
    <div className="input">
      {label && <label className="input__title">{label}</label>}
      {mask ? (
        <InputMask
          mask={mask && mask}
          value={value}
          onChange={onChange}
          disabled={disabled}
          autoFocus={autoFocus}
          type={type}
        >
          {() => (
            <input
              className={`input__section${shadow ? ' shadow' : ''}${
                border ? ' border' : ''
              }${className ? ` ${className}` : ''}`}
              placeholder={placeholder}
              name={name}
              value={value}
              style={style}
              disabled={disabled}
              autoFocus={autoFocus}
              type={type}
            />
          )}
        </InputMask>
      ) : (
        <input
          className={`input__section${shadow ? ' shadow' : ''}${
            border ? ' border' : ''
          }${className ? ` ${className}` : ''}`}
          placeholder={placeholder}
          name={name}
          onChange={onChange}
          value={value}
          style={style}
          disabled={disabled}
          autoFocus={autoFocus}
          type={type}
        />
      )}
    </div>
  )
}
