import React, { useEffect, useState } from "react";
import InputMask from "react-input-mask";
import "./inputIIn.scss";

export default function InputIin({
  label = "",
  style = {},
  disabled = false,
  mask = "",
  value = "",
  onChange = null,
  placeholder = null,
  name = null,
  border = false,
  shadow = false,
  className = null,
  autoFocus = false,
  type = "text",
  callBack=null
}) {
  const [isFocus, setIsFocus] = useState(false);
  const [isTrueIIN, setIsTrueIIN] = useState(false);

  useEffect(() => {
   if (value && value.indexOf('_')<0 && className!=='error') {
     setIsTrueIIN(true);
     callBack(true);
   } 
   if (isTrueIIN && value.indexOf('_')>=0) {
     setIsTrueIIN(false);
     callBack(false);
   }
  })

  return (
    <div className="input-iin">
      {(!!value || isFocus) && (
        <label className="input-iin__title" style={{color:isTrueIIN?"#1A7C36":''}}>Введите ИИН</label>
      )}
      <InputMask
        mask={mask && mask}
        value={value}
        onChange={onChange}
        disabled={disabled}
        autoFocus={autoFocus}
        type={type}
        onFocus={() => setIsFocus(true)}
        onBlur={() => setIsFocus(false)}
      >
        {() => (
          <input
            className={`input-iin__section${shadow ? " shadow" : ""}${
              border ? " border" : ""
            }${className ? ` ${className}` : (isTrueIIN?" success":"")}`}
            placeholder={"Введите ИИН"}
            name={name}
            style={{border:"2px solid green !important"}}
            value={value}
            style={style}
            disabled={disabled}
            autoFocus={autoFocus}
            type={type}
          />
        )}
      </InputMask>
      <>
        <div></div>
        <small
          style={{
            visibility: className == "error" ? "" : "hidden",
            margin: "10px",
            color:"#ff0000"
          }}
        >
          * ИИН не найден
        </small>
      </>
    </div>
  );
}
