import React, { useState } from "react";

import {
  Box,
  Tabs,
  Tab,
  Typography,
  Container,
  Button,
} from "@material-ui/core";
import "./tabs.scss";

export default function ({ tabTitles = [{ title: "", value: 0 }], children }) {
  const [selectedValue, setSelectedValue] = useState(0);
  return (
    <>
      <Container>
        <Box mt={0}>
          <Tabs
            value={selectedValue}
            variant={"fullWidth"}
            TabIndicatorProps={{
              style: {
                backgroundColor: "#367B1E",
                wordBreak: "break-word",
                height: "3px",
              },
            }}
            style={{ maxHeight: "100px" }}
          >
            {tabTitles.map(({ title, value }, index) => (
              <Tab
                key={index}
                onClick={() => setSelectedValue(value)}
                label={
                  <Typography
                    className={`tabs__item ${
                      selectedValue == value ? "activeTab" : ""
                    }`}
                  >
                    {title}
                  </Typography>
                }
              />
            ))}
          </Tabs>
        </Box>
      </Container>
      <div className="tabs__line"></div>

      <Container>
        <Box py={0} px={0}>
          {children.map((item, index) => (
            <TabPanel value={selectedValue} index={index} key={index}>
              {item}
            </TabPanel>
          ))}
        </Box>
      </Container>
    </>
  );
}

function TabPanel(props) {
  const { children, value, index, label, ...other } = props;

  return (
    <Box mt={6} key={index}>
      <Typography
        component="div"
        //variant="h5"
        role="tabpanel"
        hidden={value !== index}
        id={`simple-tabpanel-${index}`}
      >
        {label}
      </Typography>
      <Box my={2}>{value === index && children}</Box>
    </Box>
  );
}
