import React from "react";
import { Route } from "react-router-dom";
import HomePage from "@/views/pages/homePage/page";
import PaymentWithoutSms from "@/views/pages/paymentWithoutSms/page";
import PaymentPage from "@/views/pages/paymentPage/page";

export default function () {
  return (
    <div>
      <Route path="/" component={HomePage} exact />
      <Route path="/payment-without-sms" component={PaymentWithoutSms} />
      <Route path="/payment" component={PaymentPage} />
    </div>
  );
}
