import React, { useEffect, useRef } from "react";
import { Button } from "@/components";
import { useDispatch, useSelector } from "react-redux";
import { setAlert } from "@/toolkit/alertSlice";
import { setCoborrower } from "@/toolkit/coborrowerSlice";
import request from "@/utils/request";
import { history } from "@/helpers";
import "./otpModal.scss";

export default function OtpModal(props) {
  const state = useSelector((state) => ({
    coborrower: state.default.coborrower,
  }));
  const { coborrower } = state;
  const dispatch = useDispatch();

  const [otp1, setOTP1] = React.useState("");
  const [otp2, setOTP2] = React.useState("");
  const [otp3, setOTP3] = React.useState("");
  const [otp4, setOTP4] = React.useState("");

  const [counter, setCounter] = React.useState(119);
  const [error, setError] = React.useState(false);
  const [noCode, setNoCode] = React.useState(false);

  const otp1_ref = useRef(null);
  const otp2_ref = useRef(null);
  const otp3_ref = useRef(null);
  const otp4_ref = useRef(null);

  React.useEffect(() => {
    if (otp1.length > 0) {
      otp2_ref.current.focus();
    }
    if (otp2.length > 0) {
      otp3_ref.current.focus();
    }
    if (otp3.length > 0) {
      otp4_ref.current.focus();
    }
    if (otp4.length > 0) {
      otp4_ref.current.blur();
    }
  }, [otp1, otp2, otp3, otp4]);

  // Third Attempts
  React.useEffect(() => {
    const timer =
      counter > 0 &&
      setInterval(
        () =>
          setCounter(
            parseInt(counter - 1).toString().length == 1
              ? `0${counter - 1}`
              : counter - 1
          ),
        1000
      );
    return () => clearInterval(timer);
  }, [counter]);

  const coborrowerOTPVerify = () => {
    request
      .post(`​/api/credits/applications/${props?.id}/coborrower-otp-verify/`, {
        code: `${otp1}${otp2}${otp3}${otp4}`,
      })
      .then((res) =>
        res.status >= 400
          ? res.json().then((res) => {
              dispatch(
                setAlert({
                  type: "error",
                  message:
                    res?.code[0] || "Не правильный код. Попробуйте снова.",
                  show: true,
                })
              );
            })
          : res.json().then((res) => {
              dispatch(setCoborrower(res));
            })
      );
  };

  const addClient = () => {
    setOTP1("");
    setOTP2("");
    setOTP3("");
    setOTP4("");
    otp1_ref.current.focus();
    setError(false);
    setCounter(119);
    request
      .post(`​/api/credits/applications/${5}/add-client/`, {
        phone: "+77475268576".replace(/[^+\d]/g, ""),
        iin: "850425301510".replace(/[^+\d]/g, ""),
      })
      .then((res) =>
        res.status >= 400
          ? dispatch(setAlert("error", "Не удалось отправить СМС.", true))
          : res.json().then((res) => {
              setOTP1("");
              setOTP2("");
              setOTP3("");
              setOTP4("");
              otp1_ref.current.focus();
              setError(false);
              setCounter(2);
            })
      );
  };

  if (noCode) {
    return (
      <div style={{width:"70%", marginLeft:"15%"}}>
        <h2 className="t-c">Не пришёл код</h2>
        <div className="t-c">
          <div className="text-modal">
            Убедитесь, что на телефоне хороший сигнал сотовой связи и запросите
            код повторно
          </div>
        </div>
        <h2 className="t-c" style={{ marginTop: "30px" }}>
          Новый номер телефона
        </h2>
        <ul className="t-c">
          <li className="text-modal" >
            Вы можете внести плановый платёж без СМСи без детализации по займу
          </li>
          <li className="text-modal" style={{marginTop:"15px"}}>
            Обратитесь в отделение Банка с документом, удостоверяющим личность,
            чтобы обновить номер в договоре
          </li>
        </ul>
        <div className="d-g">
        <Button
          className="empty p-y-15 m-t-50 m-b-30 m-l-20"
          label="Оплатить без СМС"
          onClick={() => history.push("/payment-without-sms")}
         
        />
      </div>
      </div>
    );
  }

  return (
    <>
      <h2 className="t-c">Введите код из SMS</h2>
      <div className="t-c">
        <p style={{ color: "#000" }}>
          Код подтверждения отправлен на номер:{" "}
          <strong>{coborrower?.phone} +7 ••• ••• 23 23</strong>
        </p>
      </div>

      <div className="otp gg-30 m-t-55 m-b-35 p-x-30">
        <div className="input">
          <input
            type="tel"
            className="input__section border p-y-30 t-c"
            value={otp1}
            style={{
              fontSize: "1.5625rem",
              background: otp1 ? "#fff" : "#EFEFEF",
              border: `1px solid ${otp1 ? "#1A8C3A" : "none"}`,
            }}
            onChange={(e) => setOTP1(e.target.value)}
            autoFocus={!otp1}
            ref={otp1_ref}
          />
        </div>
        <div className="input">
          <input
            type="tel"
            className="input__section border p-y-30 t-c"
            value={otp2}
            style={{
              fontSize: "1.5625rem",
              background: otp2 ? "#fff" : "#EFEFEF",
              border: `1px solid ${otp2 ? "#1A8C3A" : "none"}`,
            }}
            onChange={(e) => setOTP2(e.target.value)}
            ref={otp2_ref}
          />
        </div>
        <div className="input">
          <input
            type="tel"
            className="input__section border p-y-30 t-c"
            value={otp3}
            style={{
              fontSize: "1.5625rem",
              background: otp3 ? "#fff" : "#EFEFEF",
              border: `1px solid ${otp3 ? "#1A8C3A" : "none"}`,
            }}
            onChange={(e) => setOTP3(e.target.value)}
            ref={otp3_ref}
          />
        </div>
        <div className="input">
          <input
            type="tel"
            className="input__section border p-y-30 t-c"
            value={otp4}
            style={{
              fontSize: "1.5625rem",
              background: otp4 ? "#fff" : "#EFEFEF",
              border: `1px solid ${otp4 ? "#1A8C3A" : "none"}`,
            }}
            onChange={(e) => setOTP4(e.target.value)}
            ref={otp4_ref}
          />
        </div>
      </div>
      <div className="t-c">
        {error && <p className="red">Неверный СМС-код</p>}
        {counter != 0 ? (
          <div>
            <div style={{ background: "#F5F5F5", padding: "5px" }}>
              Запросить код через{" "}
              <strong style={{ color: "red" }}>
                {`0${Math.trunc(counter / 60)}`}:
                {counter - Math.trunc(counter / 60) * 60} сек
              </strong>
              .
            </div>
          </div>
        ) : (
          <a
            className="link black"
            style={{ color: "black" }}
            onClick={() => addClient()}
          >
            ЗАПРОСИТЬ КОД
          </a>
        )}
      </div>

      <div style={{display:"flex",justifyContent:"center"}}>
        <Button
          className="success p-y-15 m-t-50 m-b-30"
          label="Далее"
          onClick={() => history.push("/payment")}
          disabled={!(otp1 && otp2 && otp3 && otp4)}
        />
      </div>
      <div className="t-c" onClick={() => setNoCode(true)}>
        <p style={{ textDecoration: "underline" }}>
          Проблемы с получением кода?
        </p>
      </div>
    </>
  );
}
