import React, { useState } from "react";
import Modal from "react-modal";
import SVG from "react-inlinesvg";
import Close from "public/close.svg";

import "./modal.scss";

export default function (props) {
  const mobile = window.innerWidth <600;
  let baseOverFlow = {
    base: "overlay-base",
    afterOpen: "overlay-base_after-open",
    beforeClose: "overlay-base_before-close",
  };

  if (mobile) {
    baseOverFlow = {
      base: "overlay-base-mobile",
      afterOpen: "overlay-base_after-open",
      beforeClose: "overlay-base_before-close",
    };
  
  }
 
  return (
    <Modal
      ariaHideApp={false}
      isOpen={props.isOpen}
      onRequestClose={props.onRequestClose}
      contentLabel="Modal"
      className={{
        base: `modal-base modal-base__box${
          props.modalView ? props.modalView : ""
        } `,
        afterOpen: "modal-base_after-open",
        beforeClose: "modal-base_before-close",
      }}
      style={{content:props.style}}
     
      overlayClassName={baseOverFlow}

      shouldCloseOnOverlayClick={false}
      closeTimeoutMS={10}
    >
      <div className="modalContent">
        <div className="modalContent-wrapperSVG">
          <div
            className="modalContent-wrapperSVG__SVG"
            onClick={() => props.handleFunc({ isOpen: false })}
          >
            <SVG src={Close} />
          </div>
        </div>
        {props.children}
      </div>
    </Modal>
  );
}
