import React from "react";
import SVG from "react-inlinesvg";
import Up from "public/up.svg";
import Down from "public/down.svg";
import "./accordion.scss";

export default function ({ faqItems }) {
  return (
    <article className="faq">
      <h1 className="faq-heading">Часто задаваемые вопросы</h1>
      {faqItems.map(({ title, text }, index) => (
        <AccordionItem title={title} text={text} key={index} />
      ))}
    </article>
  );
}

function AccordionItem({ title, text }) {
  const [isOpen, setIsOpen] = React.useState(false);

  const styleCondition = isOpen
    ? { background: "#f9f9f9" }
    : { background: "#fff" };

  return (
    <section
      className="faq-item"
      style={styleCondition}
      onClick={() => setIsOpen(!isOpen)}
    >
      <div className="faq-item-title">
        <h3>{title}</h3>
        <div className="faq-item-icon">
          {isOpen ? <SVG src={Up} /> : <SVG src={Down} />}
        </div>
      </div>
      {isOpen && <p className="faq-item-text">{text}</p>}
    </section>
  );
}
